<template>
  <div id="app">
    <!-- 顶部栏区域 -->
    <mt-header fixed :title="this.$store.getters.getTitle">
      <router-link to="/" slot="left">
        <mt-button icon="back" @click="back">返回</mt-button>
      </router-link>
      <mt-button
        icon="more"
        slot="right"
        v-if="this.$store.getters.getface"
        class="face_box"
      >
        <div class="face">
          <img :src="this.$store.getters.getface" alt="" class="face_img" />
        </div>
      </mt-button>
      <mt-button icon="more" slot="right" v-else></mt-button>
    </mt-header>

    <!-- 路由组件的出口 -->
    <router-view />

    <!-- 底部栏 -->
    <mt-tabbar v-model="selected" fixed>
      <mt-tab-item id="/home">
        <img slot="icon" src="./assets/首页.png" />
        首页
      </mt-tab-item>
      <mt-tab-item id="/vip">
        <img slot="icon" src="./assets/会员 .png" />
        会员
      </mt-tab-item>
      <mt-tab-item id="/cart" class="cart">
        <span class="cartnum" v-if="!this.$store.getters.getCartSum == 0">{{
          this.$store.getters.getCartSum
        }}</span>
        <img slot="icon" src="./assets/购物车.png" />
        购物车
      </mt-tab-item>
      <mt-tab-item id="/search">
        <img slot="icon" src="./assets/搜索.png" />
        搜索
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      selected: sessionStorage.getItem("selected")
        ? JSON.parse(sessionStorage.getItem("selected"))
        : "/home",
      toptitle: "",
    };
  },
  created() {
    if(sessionStorage.getItem("login")){
    
    JSON.parse(sessionStorage.getItem("cartnum"))
      ? this.$store.commit(
          "initCartNum",
          JSON.parse(sessionStorage.getItem("cartnum"))
        )
      : "";

    JSON.parse(sessionStorage.getItem("login")).img
      ? this.$store.commit(
          "setfaceurl",
          JSON.parse(sessionStorage.getItem("login")).img
        )
      : "";
  }
  },
  watch: {
    // $route(to, from) {
    //   if (to.name == "home") {
    //     this.selected = to.fullPath;
    //   }
    //   console.log(to, from);
    // },

    // title:this.$store.getters.getTitle(),
    selected: function (newV, oldV) {
      console.log(newV, oldV);
      sessionStorage.setItem("selected", JSON.stringify(newV));
      this.$router.replace(newV);
      this.toptitle = newV;
    },
  },
  methods: {
    back(){
      this.$router.go(-1);
    }
  },
};
</script>

<style scoped>
.cart {
  position: relative;
}
.cartnum {
  position: absolute;
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #f40;
  top: 3px;
  right: 24px;
  line-height: 15px;
  text-align: center;
  color: #fff;
}
.face_box {
  overflow: visible;
}
.face {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: #fff;
  top: 15%;
  right: 5%;
  border-radius: 50%;
  overflow: hidden;
}
.face_img {
  width: 100%;
  height: 100%;
}
</style>



