<template>
  <div class="goodslist">
    <ul>
      <li v-for="(item, index) in goods">
        <div class="img_box">
          <img :src="item.imgurl" alt="" />
        </div>
        <p class="goodname">{{ item.name }}</p>
        <p class="goodprice">价格：￥{{ item.price }}</p>
        <div class="buy_box">
          <button class="changenum" @click="delnum(index)">-</button>
          <input type="text" :value="num[index].num" />
          <button class="changenum" @click="addnum(index)">+</button>
          <button class="putcart" @click="putcart(index)">加入购物车</button>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "Goods",
  data() {
    return {
      goods: "",
      num: [],
    };
  },
  created() {
    this.$store.commit("changeTitle", "商品列表");

    this.$axios
      .get("getgoods")
      .then((res) => {
        this.goods = res.data.data;
        for (let i = 0; i < this.goods.length; i++) {
          let obj = this.goods[i];
          obj.num = 1;
          this.num[i] = obj;
        }
        // console.log(this.num);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    compute(that, index, tag) {
      let lin = that.num[index];
      if (tag == "-") {
        lin.num = --lin.num;
      } else {
        lin.num = ++lin.num;
      }
      that.$set(that.num, index, lin);
    },
    delnum(index) {
      this.$options.methods.compute(this, index, "-");
    },
    addnum(index) {
      this.$options.methods.compute(this, index, "+");
    },
    putcart(index) {
      this.$store.commit("changeCartNum", this.num[index]);
      console.log(this.$store.getters.getCartNum);
    },
  },
};
</script>
<style scoped>
.goodslist {
  width: 100%;
}
ul {
  display: flex;
  flex-wrap: wrap;
}
li {
  width: 40%;
  background-color: rgb(233, 224, 224);
  border: 1px solid;
  margin: 4.5%;

}
.img_box {
  width: 100%;
  height: 150px;
}
img {
  width: 100%;
}
p {
  width: 100%;
  text-align: center;
}
.goodname {
  font-weight: bold;
}
.goodprice {
  color: #f40;
}
input {
  width: 20px;
  text-align: center;
}
.buy_box {
  width: 100%;
  height: 25px;
  text-align: center;
}
.changenum {
  width: 15px;
}
.putcart {
  color: #f40;
  border: none;
  margin-left: 7px;
}
</style>


