<template>
    <div class="message">
        <textarea cols="20" rows="10">请输入留言</textarea>
        <button>发送留言</button>
    </div>

</template>
<script>
export default {
    name:'Message',
    data(){
        return{
            text:""
        }
    },
    created(){
        this.$store.commit("changeTitle", "留言反馈");
    }
}
</script>
<style scoped>
    .message{
        text-align: center;
    }
    textarea {
        box-sizing: border-box;
        padding: 10px;
        margin-top: 10px;
        width: 90%;
        height: 150px;
        resize: none;
    }
</style>


