import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    cid: 0,
    news: "",
    faceurl: "",
    title: "",
    cartnum: [],
    cart: ""
  },
  getters: {
    getTitle(state) {
      return state.title;
    },
    getCartNum(state) {
      return state.cartnum;
    },
    getCartSum(state) {
      let sum = 0;
      state.cartnum.forEach(item => {
        sum += item.num;
      });

      return sum;
    },
    getface(state) {
      return state.faceurl;
    },
    getnew(state) {
      return state.news;
    },
    getcid(state){
      console.log(state.cid);
      return state.cid
    }
  },
  mutations: {
    changeTitle(state, newTitle) {
      state.title = newTitle;
    },
    initCartNum(state, ses) {
      state.cartnum = ses;
    },
    changeCartNum(state, newCartNume) {
      state.cartnum.push(newCartNume);
      sessionStorage.setItem("cartnum", JSON.stringify(state.cartnum));
    },
    delCartNum(state, index) {

      state.cartnum.splice(index, 1);
      sessionStorage.setItem("cartnum", JSON.stringify(state.cartnum));
    },
    setfaceurl(state, url) {
      state.faceurl = url;
      
    },
    putnews(state, newsl) {
      state.news = newsl;
      
    },
    putcid(state, index) {
      state.cid = index;
      
    }
  }
});

export default store;



