<template>
  <div class="newslist">
    <ul>
      <li v-for="(item, index) in newslist" @click="getcid(index)">
        <router-link :to="{ name: 'new' }">
          <div class="new_img">
            <img :src="item.imgsrc" alt="" />
          </div>
          <div class="content">
            <p class="title">{{ item.title }}</p>
            <div class="news-desc">
              <p class="summary">{{ item.digest }}</p>
              <p class="ite">
                <span class="spraise">点赞数：{{ item.replyCount }}</span>
                <span class="time"
                  >发表时间：{{ item.ptime | dayFormat("YYYY-MM-DD") }}</span
                >
              </p>
            </div>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "NewsList",
  data() {
    return {
      newslist: [],
    };
  },
  created() {
    this.$axios
      .get("getnewslist")
      .then((res) => {
        this.newslist = res.data.T1348647853363;
        //  console.log(this.newslist);
        this.$store.commit("putnews", this.newslist);
      })
      .catch((err) => {
        console.log("新闻列表异常", err);
      });
    this.$store.commit("changeTitle", "新闻列表");
  },
  methods: {
    getcid(index) {
      console.log(index);
      this.$store.commit("putcid",index);
    },
  },
};
</script>
<style scoped>
.newslist {
  width: 100%;

  font-size: 10px;
}
.new_img {
  width: 100px;
  height: 100px;
  float: left;
  margin-top: 4px;
}
img {
  width: 100%;
  height: 100%;
}
.content {
  float: right;
  width: 68%;
  /* background-color: red; */
}
a {
  text-decoration: none;
}
li {
  margin-top: 10px;
  padding: 10px;
  /* background-color: blue; */
  display: flex;
}
.ite {
  float: right;
  display: block;
  color: coral;
}
.title {
  font-size: 16px;
  font-weight: bold;
  color: black;
}
.summary {
  color: rgb(140, 135, 135);
}
</style>


