<template>
  <div>
    <ul>
      <li v-for="(item, index) in imgtexts">
        <div class="img_box">
          <img :src="item.imgsrc" alt="" />
        </div>
        <p>{{ item.title }}</p>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "ImgText",
  data() {
    return {
      imgtexts: [],
    };
  },
  created() {
    this.$store.commit("changeTitle", "图文分享");
    this.$axios
      .get("getimgtext")
      .then((res) => {
        this.imgtexts = res.data.T1348648517839;
        
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>
<style scoped>
li{
    position: relative;
    display: block;
    margin-top: 20px;
    
}
.img_box{
    width: 100%;
    height: 200px;
}
img{
    width: 100%;
    height: 100%;
}
p{
    background-color: rgba(0,0,0,0.6);
    width: 100%;
    position: absolute;
    bottom: 0;
    text-align: center;
    color: #fff;
}
</style>


