<template>
  <div>
    <p class="mes" v-if="cartnum.length == 0">购物车中暂无商品信息，请在商品展示中添加</p>
    <ul>
      <li v-for="(item, index) in cartnum">
        <div class="img_box">
          <img :src="item.imgurl" alt="" />
        </div>

        <div>
          <p>产品：{{item.name}}</p>
          <p>单价：￥{{item.price}}</p>
          <p>数量：{{ item.num }}</p>
          <p>价格：￥{{item.num*item.price}}</p>
        </div>
        <button @click="delcart(index)">删除</button>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "Cart",
  data() {
    return {
      cartnum: "",
    };
  },
  created() {
    this.$store.commit("changeTitle", "购物车");
    this.cartnum = this.$store.getters.getCartNum;
    // this.cartnum = JSON.parse(sessionStorage.getItem("cartnum"));
  },
  methods:{
    delcart(index){
    this.$store.commit("delCartNum", index);
      // this.cartnum.splice(index,1);
      //  sessionStorage.setItem("cartnum",JSON.stringify(this.cartnum))
    }
  }
};
</script>
<style scoped>
li {
  position: relative;
  width: 100%;
  height: 100px;
  margin-top: 10px;
  background-color: #ccc;
  
}
.img_box {
  width: 100px;
  height: 100px;
  float: left;
}
img {
  width: 100%;
  height: 100%;
}
button{
  position: absolute;
  top: 10px;
  right: 10px;
  color: red;
}
.mes{
  text-align: center;
  line-height: 100px;
}
</style>


