<template>
  <div>
    <div v-if="taget">
      <div class="login">
        用户名：<input type="text" :value="username" /><br />
        密&nbsp&nbsp&nbsp码：<input type="text" :value="password" /><br />
        <button @click="login">登录</button>
      </div>
    </div>
    <div v-else>
      我是会员
      <button @click="outline">退出登录</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "Vip",
  data() {
    return {
      username: "root",
      password: "123456",
      taget:true,
      user:{}
    };
  },
  created() {
    this.$store.commit("changeTitle", "会员中心");
    if(sessionStorage.getItem("login")){
      this.taget = !JSON.parse(sessionStorage.getItem("login")).msg
    }
    
  },
  methods: {
    login() {
      this.$axios
        .post("login", {
          username: this.username,
          password: this.password,
        })
        .then((res) => {
      
          this.user = res.data;
    
          if(res.data.msg=="ok"){
            this.taget = false
          }

          this.$store.commit("setfaceurl",res.data.img)
          sessionStorage.setItem("login",JSON.stringify(res.data))
        })
        .catch((err) => {

          console.log(err);
        });

        
    },
    outline(){
      this.taget = true;
      this.$store.commit("setfaceurl",null)
    }
  },
};
</script>
<style scoped>
.login {
  padding-top: 50px;
  text-align: center;
  color: aliceblue;
  width: 100%;
  height: 100px;
  background-color: rgba(0, 0, 0, 0.6);
}
button {
  background-color: lightseagreen;
  margin-top: 10px;
  font-weight: bold;
  font-size: 16px;
  width: 100px;
  height: 30px;
}
</style>


