<template>
  <div>
    <div class="img_box">
      <img :src="this.text.imgsrc" alt="" />
    </div>

    <p>{{ this.text.digest }}</p>
  </div>
</template>
<script>
export default {
  name: "New",
  data() {
    return {
      new: "",
      cid: this.$store.getters.getcid,
      text: "",
    };
  },
  created() {
    setTimeout(() => {
      this.$store.commit("changeTitle", "新闻详情");
      this.new = this.$store.getters.getnew;
      this.text = this.new[this.$store.getters.getcid];
      console.log(this.$store.getters.getcid);
    },);
  },
};
</script>
<style scoped>
.img_box {
  width: 100%;
}
img {
  width: 100%;
}
</style>


